@import url("https://fonts.googleapis.com/css2?family=Indie+Flower&family=La+Belle+Aurore&family=Marck+Script&family=Monoton&family=Poppins:ital,wght@0,200;1,100&display=swap");

* {
  box-sizing: border-box;
  transition: all ease-in-out;
}

:root {
  --primary: #5265c4;
  --primary: #2e17ff;
}

.capitlize__txt {
  text-transform: capitalize;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.main-ctn {
  min-width: 300px;
  min-height: 550px;
}

p[role="alert"] {
  color: red;
  margin: 0.5rem 0;
}

.flexCenter {
  display: flex;
  align-items: center;
  justify-content: center;
}

.register,
.login {
  background: #0e101c;
  height: calc(100vh - 60px);
  margin-top: 0;
}

.register form,
.login form {
  width: calc(clamp(200px, 400px, 800px));
  margin: 1rem auto 0;
  flex-direction: column;
}

.register h1,
.login h1 {
  font-weight: 500;
  color: white;
  text-align: center;
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 0.4rem;
}

.register p,
.login p {
  color: red;
}

.register p::before,
.login p::before {
  display: inline;
  content: "âš  ";
}

.register input,
.login input {
  display: block;
  box-sizing: border-box;
  width: 100%;
  border-radius: 4px;
  border: 1px solid white;
  padding: 10px 15px;
  margin: 10px auto;
  font-size: 14px;
}

.register input:disabled,
.login input:disabled {
  opacity: 0.4;
}

.register input[type="submit"],
.login input[type="submit"] {
  background: var(--primary);
  color: white;
  text-transform: uppercase;
  border: none;
  padding: 20px;
  font-size: 1rem;
  font-weight: 100;
  letter-spacing: 10px;
}

.register input[type="submit"]:hover,
.login input[type="submit"]:hover {
  background: white;
  color: #333;
  cursor: pointer;
  transition: 0.2s all;
}

/* smooth scroll animation */
.hidden {
  opacity: 0;
}
.show {
  opacity: 1;
}

/* navbar show style */
.Navbar.show {
  animation: navbar-show 300ms;
}

@keyframes navbar-show {
  0% {
    opacity: 0;
    transform: translateY(-60px);
  }
  25% {
    opacity: 0.1;
    transform: translateY(-50px);
  }
  50% {
    opacity: 0.5;
    transform: translateY(-30px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}

/* main search section show style */
.main-ctn .search_ctn > h1 {
  animation: fromLeftShow 700ms ease forwards;
  opacity: 1;
}

@keyframes fromLeftShow {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }
  50% {
    opacity: 0.4;
    transform: translateX(-80%);
  }
  75% {
    opacity: 0.7;
    transform: translateX(-25%);
  }
  100% {
    opacity: 1;
    transform: translateX(0%);
  }
}

@keyframes fromRightShow {
  0% {
    opacity: 0;
    transform: translateX(100%);
  }
  50% {
    opacity: 0.4;
    transform: translateX(80%);
  }
  75% {
    opacity: 0.7;
    transform: translateX(25%);
  }
  100% {
    opacity: 1;
    transform: translateX(0%);
  }
}

@keyframes fromTopShow {
  0% {
    opacity: 0;
    transform: translateY(-100%);
  }
  50% {
    opacity: 0.4;
    transform: translateY(-50%);
  }
  75% {
    opacity: 0.7;
    transform: translateY(-25%);
  }
  100% {
    opacity: 1;
    transform: translateY(0%);
  }
}

@keyframes fromBottomShow {
  0% {
    opacity: 0;
    transform: translateY(100%);
  }
  50% {
    opacity: 0.4;
    transform: translateY(50%);
  }
  75% {
    opacity: 0.7;
    transform: translateY(25%);
  }
  100% {
    opacity: 1;
    transform: translateY(0%);
  }
}

/* map show style */
.map-ctn.show .leaflet-container {
  animation: fromRightShow 200ms ease;
}
.map-ctn.show p {
  animation: fromRightShow 250ms ease;
}

/* about show style */
.about-grid .item1.show {
  animation: fromLeftShow 100ms ease-out;
}
.about-grid .item2.show img,
.about-grid .item3.show .txt-box,
.about-grid .item4.show img {
  animation: fromLeftShow 500ms ease-out;
}
.about-grid .item2.show .txt-box,
.about-grid .item3.show img,
.about-grid .item4.show .txt-box {
  animation: fromRightShow 600ms ease-out;
}

/* service show style */
.service-section .item1.show .txt-bx,
.service-section .item2.show img {
  animation: fromLeftShow 200ms ease-out;
}
.service-section .item1.show img,
.service-section .item2.show .txt-bx {
  animation: fromRightShow 200ms ease-out;
}
.service-grid.show:nth-child(2n) {
  animation: bithImgShow 150ms ease-in-out;
}
.service-grid.show:nth-child(2n+1) {
  animation: bithImgShow 300ms ease-in-out;
}

@keyframes bithImgShow {
  0% {opacity: 0;}
  10% {opacity: 0.3;}
  50% {opacity: 0.5;}
  80% {opacity: 0.8;}
  100% {opacity: 1;}
}

/* contact form show style */
.contactForm-ctn.show h1 {
  animation: fromTopShow 300ms ease;
}
.contactForm-ctn.show .contactForm {
  animation: fromBottomShow 250ms ease;
}

/* register form show style */
.register.show h1 {
  animation: fromTopShow 300ms ease;
}
.register.show form {
  animation: fromBottomShow 250ms ease;
}

/* login form show style */
.login.show h1{
  animation: fromTopShow 300ms ease;
}
.login.show form {
  animation: fromBottomShow 250ms ease;
}

/* footer show style */
.footer.show {
  animation: footerShow 200ms ease;
}

@keyframes footerShow {
  0% {
    opacity: 0;
    transform: translateY(50%);
  }
  25% {
    opacity: 0.1;
    transform: translateY(35%);
  }
  50% {
    opacity: 0.5;
    transform: translateY(20%);
  }
  100% {
    opacity: 1;
    transform: translateY(0%);
  }
}