.Navbar {
  /* position: fixed;
  top: 0;
  left: 0;
  right: 0; */
  /* bottom: 0.5rem; */
  display: flex;
  height: 60px;
  justify-content: space-between;
  align-items: center;

  background: var(--primary);
}

.Navbar > .nav-logo {
  margin: 15px;
  margin-right: 0;

  font-weight: 900;
  font-size: 21px;

  color: #ffffff;
}

.Navbar a {
  margin: 15px;
  position: relative;

  font-weight: 700;
  font-size: 16px;
  text-decoration: none;
  opacity: 0.9;

  color: #ffffff;
}

.Navbar > .nav-items > a:hover {
  opacity: 1;
}

.Navbar > .nav-items > a::after {
  content: "";
  position: absolute;
  top: 1.3rem;
  left: 0;
  bottom: 0;
  width: 0;
  height: 2px;

  background: #ffffff;

  transition: all 0.45s;
}

.Navbar > .nav-items > a.activated::after {
  width: 100%;
}

.Navbar .log_sign a {
  padding: 10px;
  text-align: center;

  border: 1px solid;
  border-radius: 10px;
}
.Navbar .log_sign .log {
  margin-left: auto;

  background: #ffffff;
  color: var(--primary);
}

.Navbar .log_sign .log:hover {
  background: var(--primary);
  color: white;
}

.Navbar .log_sign .sign:hover {
  background: white;
  color: var(--primary);
}

.Navbar > .nav-toggle {
  display: none;
}

@media (max-width: 700px) {
  .Navbar > .nav-items {
    position: absolute;
    top: 60px;
    display: flex;
    width: 100%;
    height: calc(100vh - 60px);
    flex-direction: column;
    transform: translateX(-100%);
    left: 0;

    background: var(--primary);

    transition: all 0.45s;
  }

  .Navbar > .nav-items a {
    width: fit-content;
    top: 15%;
    left: 50%;

    transform: translate(-50%);
  }

  .Navbar > .nav-items > a::before {
    background: transparent;
  }

  .Navbar > .nav-items.open {
    transform: translateX(0);
  }

  .Navbar > .nav-toggle {
    display: flex;
    width: 50px;
    height: 50px;
    align-items: center;
    justify-content: center;

    cursor: pointer;
  }

  .nav-toggle > .bar {
    position: relative;
    width: 32px;
    height: 2px;

    background: #ffffff;

    transition: all 0.45s ease-in-out;
  }

  .nav-toggle > .bar::before,
  .nav-toggle > .bar::after {
    content: "";
    position: absolute;
    height: 2px;

    background: #ffffff;

    border-radius: 2px;
    transition: all 0.45s ease-in-out;
  }

  .nav-toggle > .bar::before {
    width: 25px;
    transform: translateY(-8px);
    right: 0;
  }

  .nav-toggle > .bar::after {
    width: 32px;
    transform: translateY(8px);
  }

  .nav-toggle.open > .bar {
    transform: translateX(-40px);

    background: transparent;
  }

  .nav-toggle.open > .bar::before {
    width: 32px;
    transform: rotate(45deg) translate(26px, -26px);
  }

  .nav-toggle.open > .bar::after {
    transform: rotate(-45deg) translate(26px, 26px);
  }

  .log_sign {
    position: absolute;
    display: flex;
    width: 50%;
    flex-direction: column;
    left: 50%;
    top: 50%;
    transform: translateX(-50%);

    background: transparent;

    transition: all 0.45s ease-in-out;
  }

  .log_sign a {
    position: absolute;
    width: 50%;
    transform: translateX(-50%);
  }

  .log_sign .log {
    left: 55%;
    margin-left: 10rem;
  }

  .log_sign .sign {
    left: 50%;
    margin-top: 6rem;
  }

  .log_sign.open {
    opacity: 0;
  }
}
